




























































































































import uniSegmentedControl from '../../../components/uni-tab-nav.vue';
export default {
	data() {
		return {
			planInfo:{
				z_total_money: 0,
				z_yes_money: 0,
				z_no_money: 0,
				creditCard: [],
				yes_repay: [],
				no_repay: []
			},
			items: [
				'待定计划',
				'已定计划',
				'失败计划'
			],
			current: 0,
			activeColor: '#fe5950',
			styleType: 'text',
		}
	},
	onLoad() {

	},
	onShow() {
		this.getPlan();
	},
	methods: {
		goRepayDetail(credit_id,pro_id){
			uni.navigateTo({
				url: '/pages/card/credit/repayDetail?credit_id='+credit_id+'&pro_id='+pro_id
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		goTorepay(credit_id){
			uni.navigateTo({
				url: '/pages/card/credit/info?credit_id='+credit_id
			})
		},
		getPlan() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			try {
				this.$http.post(this.websiteUrl + '/api/repay/getPlan', {
					openid,
					sessionKey
				}, (res) => {
					if (res.data.code == 1) {
						this.planInfo = res.data.result;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		}
	},
	components: {
		uniSegmentedControl
	}
}
